"use client"

import { useState, useEffect, useRef } from 'react'
import { Button } from '@/components/ui/button'
import { Card } from '@/components/ui/card'

interface Message {
  type: 'user' | 'assistant'
  content: string
  timestamp: Date
}

export default function PersonalAssistant() {
  const [isCallActive, setIsCallActive] = useState(false)
  const [isListening, setIsListening] = useState(false)
  const [isProcessing, setIsProcessing] = useState(false)
  const [messages, setMessages] = useState<Message[]>([])
  const [error, setError] = useState<string | null>(null)
  const [currentTranscript, setCurrentTranscript] = useState('')

  const videoRef = useRef<HTMLVideoElement>(null)
  const mediaStreamRef = useRef<MediaStream | null>(null)
  const recognitionRef = useRef<SpeechRecognition | null>(null)

  // Initialize camera
  const startCamera = async () => {
    try {
      const stream = await navigator.mediaDevices.getUserMedia({ 
        video: true, 
        audio: true 
      })
      mediaStreamRef.current = stream
      if (videoRef.current) {
        videoRef.current.srcObject = stream
      }
      setError(null)
    } catch (err) {
      setError('Camera access denied. Please allow camera permissions.')
      console.error('Camera error:', err)
    }
  }

  // Stop camera
  const stopCamera = () => {
    if (mediaStreamRef.current) {
      mediaStreamRef.current.getTracks().forEach(track => track.stop())
      mediaStreamRef.current = null
    }
    if (videoRef.current) {
      videoRef.current.srcObject = null
    }
  }

  // Initialize speech recognition
  const initializeSpeechRecognition = () => {
    if ('SpeechRecognition' in window || 'webkitSpeechRecognition' in window) {
      const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition
      const recognition = new SpeechRecognition()
      
      recognition.continuous = true
      recognition.interimResults = true
      recognition.lang = 'en-US'

      recognition.onstart = () => {
        setIsListening(true)
        setError(null)
      }

      recognition.onresult = (event) => {
        let interimTranscript = ''
        let finalTranscript = ''

        for (let i = event.resultIndex; i < event.results.length; i++) {
          const transcript = event.results[i][0].transcript
          if (event.results[i].isFinal) {
            finalTranscript += transcript
          } else {
            interimTranscript += transcript
          }
        }

        setCurrentTranscript(interimTranscript)

        if (finalTranscript) {
          handleUserMessage(finalTranscript)
          setCurrentTranscript('')
        }
      }

      recognition.onerror = (event) => {
        setError(`Speech recognition error: ${event.error}`)
        setIsListening(false)
      }

      recognition.onend = () => {
        setIsListening(false)
        if (isCallActive) {
          // Restart recognition if call is still active
          setTimeout(() => {
            if (isCallActive) {
              recognition.start()
            }
          }, 100)
        }
      }

      recognitionRef.current = recognition
    } else {
      setError('Speech recognition not supported in this browser')
    }
  }

  // Handle user message
  const handleUserMessage = async (transcript: string) => {
    if (!transcript.trim()) return

    const userMessage: Message = {
      type: 'user',
      content: transcript,
      timestamp: new Date()
    }

    setMessages(prev => [...prev, userMessage])
    setIsProcessing(true)

    try {
      const response = await fetch('/api/assistant', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ message: transcript }),
      })

      if (!response.ok) {
        throw new Error('Failed to get AI response')
      }

      const data = await response.json()
      
      const assistantMessage: Message = {
        type: 'assistant',
        content: data.response,
        timestamp: new Date()
      }

      setMessages(prev => [...prev, assistantMessage])
      
      // Text-to-speech
      speakText(data.response)
      
    } catch (err) {
      setError('Failed to process your message. Please try again.')
      console.error('API error:', err)
    } finally {
      setIsProcessing(false)
    }
  }

  // Text-to-speech
  const speakText = (text: string) => {
    if ('speechSynthesis' in window) {
      const utterance = new SpeechSynthesisUtterance(text)
      utterance.rate = 0.9
      utterance.pitch = 1
      utterance.volume = 0.8
      speechSynthesis.speak(utterance)
    }
  }

  // Start call
  const startCall = async () => {
    await startCamera()
    initializeSpeechRecognition()
    setIsCallActive(true)
    
    if (recognitionRef.current) {
      recognitionRef.current.start()
    }

    // Welcome message
    const welcomeMessage: Message = {
      type: 'assistant',
      content: 'Hello! I\'m your personal assistant. Feel free to share your thoughts or ask me anything.',
      timestamp: new Date()
    }
    setMessages([welcomeMessage])
    speakText(welcomeMessage.content)
  }

  // End call
  const endCall = () => {
    setIsCallActive(false)
    setIsListening(false)
    stopCamera()
    
    if (recognitionRef.current) {
      recognitionRef.current.stop()
    }
    
    if ('speechSynthesis' in window) {
      speechSynthesis.cancel()
    }
  }

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      endCall()
    }
  }, [])

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 dark:from-gray-900 dark:to-gray-800">
      <div className="container mx-auto px-4 py-8">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-900 dark:text-white mb-2">
            AI Personal Assistant
          </h1>
          <p className="text-lg text-gray-600 dark:text-gray-300">
            Share your thoughts and stories in a natural conversation
          </p>
        </div>

        <div className="grid lg:grid-cols-2 gap-8 max-w-7xl mx-auto">
          {/* Video Section */}
          <Card className="p-6">
            <div className="aspect-video bg-black rounded-lg overflow-hidden mb-4 relative">
              <video
                ref={videoRef}
                autoPlay
                muted
                playsInline
                className="w-full h-full object-cover"
              />
              {!isCallActive && (
                <div className="absolute inset-0 flex items-center justify-center bg-gray-900 bg-opacity-50">
                  <p className="text-white text-lg">Camera Off</p>
                </div>
              )}
              
              {/* Status Indicators */}
              <div className="absolute top-4 left-4 space-y-2">
                {isListening && (
                  <div className="bg-red-500 text-white px-3 py-1 rounded-full text-sm flex items-center">
                    <div className="w-2 h-2 bg-white rounded-full mr-2 animate-pulse"></div>
                    Listening...
                  </div>
                )}
                {isProcessing && (
                  <div className="bg-blue-500 text-white px-3 py-1 rounded-full text-sm flex items-center">
                    <div className="w-2 h-2 bg-white rounded-full mr-2 animate-pulse"></div>
                    Processing...
                  </div>
                )}
              </div>
            </div>

            {/* Current Transcript */}
            {currentTranscript && (
              <div className="bg-gray-100 dark:bg-gray-800 p-3 rounded-lg mb-4">
                <p className="text-sm text-gray-600 dark:text-gray-400 mb-1">You're saying:</p>
                <p className="text-gray-900 dark:text-white">{currentTranscript}</p>
              </div>
            )}

            {/* Controls */}
            <div className="flex justify-center space-x-4">
              {!isCallActive ? (
                <Button 
                  onClick={startCall}
                  size="lg"
                  className="bg-green-600 hover:bg-green-700 text-white px-8"
                >
                  Start Call
                </Button>
              ) : (
                <Button 
                  onClick={endCall}
                  size="lg"
                  variant="destructive"
                  className="px-8"
                >
                  End Call
                </Button>
              )}
            </div>

            {/* Error Display */}
            {error && (
              <div className="mt-4 p-3 bg-red-100 dark:bg-red-900 border border-red-300 dark:border-red-700 rounded-lg">
                <p className="text-red-700 dark:text-red-300 text-sm">{error}</p>
              </div>
            )}
          </Card>

          {/* Conversation Section */}
          <Card className="p-6">
            <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-4">
              Conversation
            </h2>
            
            <div className="h-96 overflow-y-auto space-y-4 mb-4">
              {messages.length === 0 ? (
                <div className="text-center text-gray-500 dark:text-gray-400 py-8">
                  <p>Start a call to begin your conversation</p>
                </div>
              ) : (
                messages.map((message, index) => (
                  <div
                    key={index}
                    className={`flex ${message.type === 'user' ? 'justify-end' : 'justify-start'}`}
                  >
                    <div
                      className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
                        message.type === 'user'
                          ? 'bg-blue-600 text-white'
                          : 'bg-gray-200 dark:bg-gray-700 text-gray-900 dark:text-white'
                      }`}
                    >
                      <p className="text-sm">{message.content}</p>
                      <p className={`text-xs mt-1 ${
                        message.type === 'user' 
                          ? 'text-blue-100' 
                          : 'text-gray-500 dark:text-gray-400'
                      }`}>
                        {message.timestamp.toLocaleTimeString()}
                      </p>
                    </div>
                  </div>
                ))
              )}
            </div>

            {/* Call Status */}
            <div className="text-center">
              <p className="text-sm text-gray-600 dark:text-gray-400">
                {isCallActive 
                  ? 'Call is active - speak naturally to interact'
                  : 'Call is inactive'
                }
              </p>
            </div>
          </Card>
        </div>
      </div>
    </div>
  )
}
